









































































import Vue from 'vue'
import { routes } from './router'

export default Vue.extend({
  name: 'MyApp',
  data () {
    return {
      title: 'Examples',
      leftDrawerVisible: false,
      rightDrawerVisible: false,
      routes: routes
    }
  },
  methods: {
    toggleLeftDrawer () {
      this.leftDrawerVisible = !this.leftDrawerVisible
    },
    toggleRightDrawer () {
      this.rightDrawerVisible = !this.rightDrawerVisible
    }
  },
  components: {
  }
})
